@mixin cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

@mixin contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

@mixin popup {
	@include flex_block(row, nowrap, flex-start);

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;
	padding: 6rem var(--gl-indent);

	overflow-y: auto;

	background-color: rgba($black, 0.75);

	opacity: 0;
	pointer-events: none;
	transition: opacity 0.3s; // Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð¼Ð¸Ð»Ð¸ÑÐµÐºÑƒÐ½Ð´ Ð´Ð¾Ð»Ð¶Ð½Ð¾ ÑÐ¾Ð²Ð¿Ð°Ð´Ð°Ñ‚ÑŒ Ñ "const timeout" (300) Ð² initPopup.js

	z-index: 1000;

	&.active_state {
		opacity: 1;
		pointer-events: initial;
	}
}
